.StarWars {
    width: 80%;
    margin: 0 auto;
}

.StarWars-logo {
    width: 100%;
    max-width: 300px;
    margin-top: 1rem;
}

.StarWars input {
    font-family: "Gentium Book Basic", serif;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 80%;
    padding: 10px;
    font-size: 24px;
    background-clip: padding-box;
    border-color: rgba(0,0,0,0.25);
    border-radius: 5px;
    line-height: 20px;
    border: 1px solid #d1d5da;
}

.StarWars button {
    color: white;
    margin-top: 10px;
    width: 80%;
    padding: 10px;
    font-size: 24px;
    border-radius: 5px;
    border: 1px solid #01638f;
    margin-bottom: 50px;
    background-color: #0184be;
    font-family: "Gentium Book Basic", serif;
}

.NavLink {
    font-family: "Gentium Book Basic", serif;
    display: inline-block;
    padding: 0 15px;
    color: #02b1ff;
    text-decoration: none;
    font-size: 25px;
}

.NavLink.active {
  color: #F79824;
}

.NavLink.active:hover {
  color: #FDCA40;
}

.NavLink:hover {
  color: #0184be;
}


